.button-scroll-top {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    opacity: 0;
    color: var(--accent-color);

    position: fixed;
    bottom: 50px;
    right: 15px;
    z-index: 999;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 4px solid var(--accent-color);

}

.button-scroll-icon {
    fill: currentColor;
}

.is-show {
    opacity: 1;
    transition: 0.6s;
}